.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 320px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 40px;
  }
  @media screen and (min-width: 1344px) {
    width: 1344px;
    padding: 0 15px;
  }
}
